
import { defineComponent, PropType } from "vue";

export default defineComponent({
  props: {
    isActive: {
      type: Boolean,
      required: true,
    },
    type: {
      type: String as PropType<"warning" | "success">,
      required: true,
    },
    message: {
      type: String,
      required: true,
    },
  },
});
